import Disposition from "jx/core/layout/Disposition";
let instance = null;
let singletonEnforcer = class SingetonEnforcer {
};
/**
 * Contains functions to manipulate the disposition of components in many ways.
 */
export default class Layout {
    constructor({ jx }) {
        this.jx = jx;
        //if(enforcer != singletonEnforcer) throw "Cannot construct singleton";
        this.disposition = new Disposition(this.jx);
    }
    static getInstance(jxEngine) { if (!this[instance]) {
        this[instance] = new Layout(singletonEnforcer, jxEngine);
    } return this[instance]; }
}
